@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@300;500;700&family=Roboto:wght@100;300;400;500;700;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Courgette&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@300;500;700&family=Roboto:wght@100;300;400;500;700;900&display=swap);
:root {
  --grey-light: #c0c0c0;
  --grey-dark: #262626;
}

* {
  box-sizing: border-box;
  margin: 0;
}

/* NAVBAR STYLING */

/* NAVBAR WEBSITE (VERTICAL) */

.nav-web {
  transform-origin: left top;
  transform: rotate(-90deg) translateX(-100%);
  width: 100vh;
  height: 15vh;
  background-color: white;
  position: absolute;
  z-index: 10;
}

.nav-web ul {
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  justify-content: center;
  list-style: none;
}

.nav-web ul li {
  padding: 0.5rem 2rem;
  text-transform: uppercase;
  font-size: 1rem;
  font-family: "Oswald", sans-serif;
}

.nav-web ul li a:hover {
  color: #262626;
  color: var(--grey-dark);
  text-decoration: none;
}

.nav-web ul li a {
  color: #c0c0c0;
  color: var(--grey-light);
}

.nav-web .logo {
  font-family: "Roboto", sans-serif;
  font-weight: 800;
  font-size: 2.5rem;
  display: flex;
  flex: 0 1;
  flex-basis: 50px;
  content: " ";
  transform: rotate(90deg);
}

.nav-web ul li a.active {
  color: #262626;
  color: var(--grey-dark);
}

/* NAVBAR MOBILE (HORIZONTAL) */

.nav-mobile{
  z-index: 100;
  display: none;
}

.navbar-nav li a {
  text-transform: uppercase;
  color: #262626;
  font-family: Oswald;
  font-size: 1.5rem;
}

.navbar-brand a {
  text-transform: uppercase;
  color: #262626;
  font-size: 2.5rem;
  font-weight: 800;
}

.navbar-brand a:hover {
  color: #262626;
}

.navbar{
  display: none !important;
}

.bg-light {
  background-color: white !important;
}

.nav-mobile ul li a.active {
  text-decoration: underline;
}

/* RESPONSIVE SIZES */

@media screen and (max-width: 992px) {
  .nav-web {
    display: none;
    margin-right: 0;
  }

  .navbar{
    display: block !important;
    margin-right: 0;
  }
}

/* PRIMARY COLORS */

:root {
  --grey-light: #c0c0c0;
  --grey-dark: #262626;
  /* BACKGROUNDS */
  --pastel-blue: #a5c8e4;
  --pastel-green: #c0eccc;
  --pastel-yellow: #f9f0c1;
  --pastel-orange: #f4cda6;
  --pastel-red: #f6a8a6; 
}

.App {
  text-align: center;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

.navbar-div {
  height: 0;
}

.fullpage {
  overflow: hidden;
  margin-left: 15vh;
  height: 100vh;
}

.fullpage h1 {
  font-family: 'Oswald', sans-serif;
  color: #262626;
  color: var(--grey-dark);
  text-transform: uppercase;
  font-size: 4rem;
  margin: 1.5rem 0;
}

.fullpage h2 {
  font-family: 'Oswald', sans-serif;
  color: #262626;
  color: var(--grey-dark);
  text-transform: uppercase;
  font-size: 1.5rem;
}

.fullpage p{
  font-family: 'Roboto', sans-serif;
  font-size: 1rem;
  font-weight: 300;
  color: #262626;
  color: var(--grey-dark);
  line-height: 1.5;
}

/* BUTTON STYLING */

.fullpage .btn {
  width: 10rem;
  background-color: white;
  border: none;
  border-radius: 0;
  padding: 0.5rem 0.75rem;
}

.fullpage .btn:hover {
  color: white;
  border: none;
  transition: background-color 0.25s ease-in;
}

.fullpage .btn:focus, .fullpage .btn:active{
  outline: none;
}

/* LINE STYLING */

.line {
  height: 10px;
  width: 100px;
  border-bottom: 3px solid #262626;
  border-bottom: 3px solid var(--grey-dark);
  margin-bottom: 1.5rem;
}

.line:hover {
  width: 250px;
  transition: 1s ease-in-out;
}

/* MOBILE VERSION */

@media only screen and (max-width: 992px) {
  .fullpage {
    margin-left: 0;
    overflow-y: visible;
    overflow-y: initial;
    height: 100vh;
  }
}

.homepage {
  /* background-color: var(--pastel-blue); */
  background-image: url(/static/media/17545.dcd0e92b.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.homepage h1 {
  font-size: 7rem;
  margin-bottom: 0rem;
}

.homepage h2 {
  margin: 1rem 0;
}

.homepage .graphic {
  font-family: 'Courgette', cursive;
  text-transform: lowercase;
  font-size: 2rem;
}

.homepage .developer {
  font-family: 'Source Code Pro', monospace;
  font-size: 1.75rem;
  text-transform: uppercase;
  letter-spacing: -3px;
}

/* BUTTON HOMEPAGE */

.homepage button {
  color: #84a0b6;
  margin-top: 1.5rem;
}

.homepage button:hover {
  background-color: #84a0b6;
}

/* TEXT SELECTION COLOR AND BACKGROUND */

.homepage h1::selection,
.homepage h2::selection,
.homepage button::selection,
.homepage .developer::selection,
.homepage .graphic::selection {
  color: white;
  background-color: var(--grey-dark);
}

/* RESPONSIVE SIZES */

@media only screen and (max-width: 992px) {
  .homepage{
    background-image: none;
    background-color: var(--pastel-blue);
  }
  
  .homepage h1 {
    padding: 0 2rem;
    font-size: 5.5rem;
    line-height: 1;
  }

  .homepage .graphic,
  .homepage .developer {
    display: block;
    margin: 0.5rem 0;
  }
}

.aboutpage {
  background-image: url(/static/media/17545-02.b3aeacd1.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.aboutpage .row {
