}

.photo {
  background-image: url(/static/media/profile.dc760005.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  width: 100%;
  min-height: 35rem;
}

.about-text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.aboutpage h2 {
  margin: 1rem 0 1.5rem 0;
}

.aboutpage p {
  padding: 0 5rem;
  margin-bottom: 1rem;
}

.about-text span {
