}

/* ICONS STYLING */

.icons {
  display: flex;
  flex-direction: row;
}

.icons .icon-paragraph {
  padding: 0 1rem;
  font-size: 1.3rem;
  position: relative;
  display: inline-block;
  color: #86a58e;
}

.icons .icon-paragraph:hover {
  transform: rotate(10deg);
  transition: transform 0.5s ease-in-out;
}

#iconTip,
#swimTip,
#sportTip,
#foodTip {
  font-family: 'Oswald', sans-serif;
  text-transform: uppercase;
}

/* RESPONSIVE SIZES */

@media only screen and (max-width: 992px) {
  .aboutpage {
    background-image: none;
    background-color: var(--pastel-green);
    padding-bottom: 3rem;
    overflow-x: hidden;
  }

  .aboutpage p {
    padding: 0 2rem;
  }

  .aboutpage h1 {
    padding: 0 2rem;
  }

  .photo {
    width: 100%;
    margin: 0;
    padding: 0;
  }
}

